import { MapManager } from '../map-manager';

export const MAP_SDK = 'https://maps.api.2gis.ru/2.0/loader.js?pkg=full';

export class DoubleGisMapManager extends MapManager {

  /**
   * @type {DoubleGisMapManager}
   * @private
   */
  static _instance = null;

  /**
   * @return {DoubleGisMapManager}
   */
  static getManager () {
    if (this._instance) {
      return this._instance;
    }

    return ( this._instance = new DoubleGisMapManager() );
  }

  /**
   * @return {Promise<*>}
   */
  load () {
    return this.loadSdk( MAP_SDK );
  }

  /**
   * @return {Promise<any>}
   */
  onSdkReady () {
    return this.sdk; // DG is Promise type
  }

  /**
   * @param {string} domId
   * @param {*} options
   */
  createMap (domId, options = {}) {
    return this.sdk.map(domId, options);
  }

  /**
   * @param {*} map
   * @param {Array<Array<number>>} points
   */
  createPlacemarks (map, points = []) {
    /* const placemarkIcon = this.sdk.icon({
      iconUrl: 'my-icon.png',
      iconRetinaUrl: 'my-icon@2x.png',
      iconSize: [38, 95],
      iconAnchor: [22, 94],
      popupAnchor: [-3, -76],
      shadowUrl: 'my-icon-shadow.png',
      shadowRetinaUrl: 'my-icon-shadow@2x.png',
      shadowSize: [68, 95],
      shadowAnchor: [22, 94]
    });*/

    for (let i = 0; i < points.length; ++i) {
      let point = points[ i ];
      let placemark = this._createCustomPlacemark( point );
      placemark.addTo( map );
    }
  }

  /**
   * @return {string}
   */
  get sdkObjectName () {
    return 'DG';
  }

  /**
   * @param {number[]|{coord: number[], name: string}} point
   * @return {*}
   * @private
   */
  _createCustomPlacemark (point) {
    let coord = null;
    if (Array.isArray( point )) {
      coord = point;
    } else {
      coord = point.coord;
    }
    return DG.marker( coord );
  }
}



