<script>
  import RSmartRender from '../RSmartRender/RSmartRender.vue';
  import { UniversalMap } from "./universal-map";

  export default {
    name: 'r-map',

    components: {
      RSmartRender
    },

    props: {
      provider: {
        type: String,
        default: 'yandex'
      },
      width: {
        type: [ String, Number ],
        default: () => '100%'
      },
      height: {
        type: [ String, Number ],
        default: () => '300px'
      },
      center: {
        type: Array,
        default: () => [ 55.76, 37.64 ]
      },
      zoom: {
        type: Number,
        default: 10
      },
      points: {
        type: Array,
        default: () => []
      },
      visibilityObserver: Boolean
    },

    data: () => ({
      mapDomId: 'empty',
      loaded: false,
      nonreactive: new Map()
    }),

    methods: {
      onRender () {
        const map = new UniversalMap({ provider: this.provider });

        this.mapDomId = map.manager.nextDomId;

        this.nonreactive.set('map', map);
        this.nonreactive.set('manager', map.manager);

        this.manager.load().then(_ => this.onSdkLoaded());
      },

      onSdkLoaded () {
        this.nonreactive.set('sdk', this.manager.sdk);

        this.loaded = true;
        this.createMap();

        if (this.points && this.points.length) {
          this.createPlacemarks();
        }
      },

      createMap () {
        this.map.createMap(this.domIdentifier, {
          center: this.center,
          zoom: this.zoom
        });
      },

      createPlacemarks () {
        this.map.createPlacemarks( this.points );
      }
    },

    computed: {
      domIdentifier () {
        return `r-${this.mapDomId}`;
      },

      computedWidth () {
        if (typeof this.width === 'number') {
          return `${this.width}px`;
        }
        return this.width;
      },

      computedHeight () {
        if (typeof this.height === 'number') {
          return `${this.height}px`;
        }
        return this.height;
      },

      computedClasses () {
        return {
          'r-map': true,
          'r-map_not-loaded': !this.loaded
        };
      },

      computedStyles () {
        return {
          width: this.computedWidth,
          height: this.computedHeight
        };
      },

      sdk () {
        return this.nonreactive.get('sdk');
      },

      map () {
        return this.nonreactive.get('map');
      },

      manager () {
        return this.nonreactive.get('manager');
      }
    }
  };
</script>

<template>
  <r-smart-render onlyBrowser
                  :elementWidth="width"
                  :elementHeight="height"
                  :class="computedClasses"
                  :visibilityObserver="visibilityObserver" @render="onRender">
    <div class="r-map__map" :style="computedStyles" :id="domIdentifier"></div>
  </r-smart-render>
</template>

<style lang="scss">
  @import "../../styles/components/_map.scss";
</style>



