import { ResourceLoader } from '../../util/resource-manager';

export class MapManager {

  /**
   * @type {number}
   * @private
   */
  _currentId = 1;

  /**
   * @type {boolean}
   * @private
   */
  _sdkLoaded = false;

  /**
   * @type {boolean}
   * @private
   */
  _sdkLoading = false;

  /**
   * @type {Array}
   * @private
   */
  _queue = [];

  /**
   * @type {ResourceLoader}
   * @private
   */
  _resourceLoader = null;

  constructor () {
    this._resourceLoader = new ResourceLoader();
  }

  /**
   * @return {Promise<*>}
   */
  async loadSdk (sdkUrl) {
    if (this._sdkLoaded) {
      return this.sdk;
    } else if (this._sdkLoading) {
      return this._enqueue();
    }

    this._sdkLoading = true;

    return this._loadResource( sdkUrl ).then(resource => {
      return this.onSdkReady();
    }).then(_ => {
      const sdk = this.sdk;

      this._sdkLoaded = true;
      this._sdkLoading = false;

      this._flushQueue( sdk );

      return sdk;
    }).catch(error => {
      this._flushQueue( null, error );

      throw error; // pass next
    });
  }

  /**
   * Abstract method for sdk ready
   *
   * @abstract
   * @return {Promise<void>}
   */
  onSdkReady () {
    console.error('Not implemented');
  }

  /**
   * @param {string} domId
   * @param {*} options
   */
  createMap (domId, options = {}) {
    console.error('Not implemented');
  }

  /**
   * @param {*} map
   * @param {Array<Array<number>>} points
   */
  createPlacemarks (map, points = []) {
    console.error('Not implemented');
  }

  /**
   * @return {number}
   */
  get nextId () {
    return this._currentId++;
  }

  /**
   * @return {string}
   */
  get nextDomId () {
    return `${this.sdkObjectName}-${this.nextId}`;
  }

  /**
   * @return {boolean}
   */
  get sdkLoaded () {
    return this._sdkLoaded;
  }

  /**
   * @return {*}
   */
  get sdk () {
    return window && window[ this.sdkObjectName ];
  }

  /**
   * @abstract
   * @return {string}
   */
  get sdkObjectName () {
    throw new Error( 'Sdk name not implemented' );
  }

  /**
   * @param url
   * @return {Promise<any>}
   * @private
   */
  async _loadResource (url) {
    if (typeof window === 'undefined') {
      return;
    }

    return this._resourceLoader.load(url, 20);
  }

  /**
   * @return {Promise<any>}
   * @private
   */
  _enqueue () {
    return new Promise((resolve, reject) => {
      this._queue.push([ resolve, reject ]);
    });
  }

  /**
   * @private
   */
  _flushQueue (resolved, rejected) {
    this._queue.forEach(([resolve, reject]) => {
      if (resolved) {
        resolve( resolved );
      } else if (rejected) {
        reject( rejected );
      } else {
        resolve();
      }
    });

    this._queue = [];
  }
}



