export class ResourceLoader {

  /**
   * @param {string} resourceUrl
   * @param {number} attemptsNumber
   * @returns {Promise<*>}
   */
  async load (resourceUrl, attemptsNumber = 15) {
    return this.tryLoad(resourceUrl, attemptsNumber);
  }

  /**
   * @param {string} resourceUrl
   * @param {number} attemptsNumber
   * @returns {Promise<*>}
   */
  tryLoad (resourceUrl, attemptsNumber) {
    return this.tryUntil(attemptNumber => {
      return this._loadResource(resourceUrl);
    }, attemptsNumber);
  }

  /**
   * @param {function} asyncAction
   * @param {number} maxAttemptsNumber
   * @returns {Promise<*>}
   */
  async tryUntil (asyncAction, maxAttemptsNumber) {
    let attempts = 0;

    while (attempts < maxAttemptsNumber) {
      try {
        const result = await asyncAction(attempts); // split by variable to prevent unhandled errors
        return result;
      } catch (e) {
        attempts++;
        await this._delay(125 * Math.min(10, attempts) ** 2 + 500);
      }
    }
  }

  /**
   * @param {string} resourceUrl
   * @return {Promise<any>}
   * @private
   */
  _loadResource (resourceUrl) {
    return new Promise((resolve, reject) => {
      const resourceLoader = document.createElement('script');
      resourceLoader.type = 'text/javascript';

      if (resourceLoader.readyState) {  // IE
        resourceLoader.onreadystatechange = () => {
          if (resourceLoader.readyState === 'loaded' || resourceLoader.readyState === 'complete') {
            resourceLoader.onreadystatechange = null;
            resolve( resourceLoader );
          }
          console.log( resourceLoader.readyState, resourceLoader.readyState );
        };
      } else {  // other browsers
        resourceLoader.onload = () => resolve( resourceLoader );
        resourceLoader.onerror = () => reject( resourceLoader );
      }

      resourceLoader.src = resourceUrl;

      document.getElementsByTagName( 'head' )[0].appendChild( resourceLoader );
    });
  }

  /**
   * @param {number} delayMs
   * @private
   */
  _delay (delayMs = 0) {
    return new Promise(resolve => setTimeout(resolve, delayMs));
  }
}



