import { MapManager } from '../map-manager';

export const MAP_SDK = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU';

export class YaMapManager extends MapManager {

  /**
   * @type {YaMapManager}
   * @private
   */
  static _instance = null;

  /**
   * @return {YaMapManager}
   */
  static getManager () {
    if (this._instance) {
      return this._instance;
    }

    return ( this._instance = new YaMapManager() );
  }

  /**
   * @return {Promise<*>}
   */
  load () {
    return this.loadSdk( MAP_SDK );
  }

  /**
   * @return {Promise<any>}
   */
  onSdkReady () {
    return new Promise(resolve => this.sdk.ready( resolve ));
  }

  /**
   * @param {string} domId
   * @param {*} options
   */
  createMap (domId, options = {}) {
    options = Object.assign({}, options, {
      controls: ['zoomControl', 'fullscreenControl']
    });

    return new this.sdk.Map(domId, options);
  }

  /**
   * @param {*} map
   * @param {Array<Array<number>>} points
   */
  createPlacemarks (map, points = []) {
    for (let i = 0; i < points.length; ++i) {
      let point = points[ i ];
      let placemark = this._createCustomPlacemark( point );
      map.geoObjects.add( placemark );
    }
  }

  /**
   * @return {string}
   */
  get sdkObjectName () {
    return 'ymaps';
  }

  /**
   * @param {number[]|{coord: number[], name: string}} point
   * @return {*}
   * @private
   */
  _createCustomPlacemark (point) {
    let coord = null, name = null;
    if (Array.isArray( point )) {
      coord = point;
    } else {
      coord = point.coord;
      name = point.name
    }

    let contentOptions = {};

    if (name) {
      Object.assign(contentOptions, {
        hintContent: name,
        balloonContent: name
      });
    }

    const placemark = new this.sdk.Placemark(coord, contentOptions, {
      preset: 'islands#icon',
      // .
      //     .
      iconLayout: 'default#image',
      //    .
      //iconImageHref: 'circle@3x.png',
      //  .
      // iconImageSize: [15, 15],
      //      
      //  "" ( ).
      iconImageOffset: [0, 0]
    });

    /* placemark.events
      .add('mouseenter', function (e) {
        //   ,  ,
        //     'target'.
        e.get('target').options.set('iconImageHref', 'https://yastatic.net/www/_/x/Q/xk8YidkhGjIGOrFm_dL5781YA.svg');
        e.get('target').options.set('iconImageSize', [20, 20]);
      })
      .add('mouseleave', function (e) {
        e.get('target').options.set('iconImageHref', 'circle@3x.png');
        e.get('target').options.set('iconImageSize', [15, 15]);
      }); */

    return placemark;
  }
}



